import React, { FC } from 'react'
import clsx from 'clsx'
import { ChevronIcon } from '../../icons/Chevron'
import { ButtonProps } from './types'

const Button: FC<ButtonProps> = ({ children, className, noCarret }) => {
  return (
    <summary>
      <div
        aria-haspopup='true'
        className={clsx('DropdownButton', className)}
        role='button'
        tabIndex={0}
      >
        {children}
        {!noCarret && <ChevronIcon />}
      </div>
    </summary>
  )
}

export default Button
